<template>
  <div class="header">
      <select name="" class="changeLang" v-model="selectRoom" @change="initRoom">
          <option :value="item.room_id" v-for="(item,index) in roomList" :key="index">{{item.room_name}}</option>
      </select>
      <marquee direction="left" scrolldelay='0' v-text="msg" class="marquee"></marquee>   
      <span v-text="info_game_start+countdown" :class="['headerTopTips',{'runing':runing}]"></span>
  </div>
</template>
<style lang="less" scoped>
.header{
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 2rem;
    background: #505050;
    .changeLang {
        display: block;
        color: #fff;
        background: url(../src/images/triangle-down.png) no-repeat right #505050;
        background-size: 15px 10px;
        background-position-y: 0.75rem;
        background-position-x: 95%;
        font-size: 14px;
        border: 0;
        outline: 0;
        height: 2rem;
        padding: 0 20px 0 10px;
        margin: 0;
        -webkit-appearance: none;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
    }
    .marquee {
        display: inline-block;
        color: #ffffff;
        line-height: 2rem;
        height: 2rem;
        overflow: hidden;
        border: 0;
        background: #505050;
        position: absolute;
        top: 0;
        left: 3rem;
        right: 5rem;
        font-size: 0.875rem;
    }
    .runing{
        background: #0f9911!important;
    }
    .headerTopTips{
        display: block;
        color: #fff;
        line-height: 2rem;
        height: 100%;
        font-size: 0.9rem;
        padding: 0 10px;
        position: absolute;
        z-index: 10;
        right: 0;
        top: 0;
        background: #ca9d39;
    }
}
</style>
<script>
import { mapMutations ,mapState} from 'vuex'
import store from '../store/index.js'
import {CHAT,ADMIN_CHAT,VIDEO_WS} from '../assets/client'
export default {
    data(){
        return {
          info_game_start:'停止下注',
          msg:'尊敬的贵宾您好，欢迎光临卡卡湾娱乐场WT厅，视频与路单同步、真人打码--真实有保障。无需下载APP，H5网页不留痕迹，摆脱微信、潮信被封--安全无忧 。24小时输入网址随时随地游戏--方便快捷。千千万万个高手集聚游戏大厅，刷屏投注--气氛浓烈，保留微投的“原汁原味”。放心、尽情、享您所想---首选卡卡湾娱乐场，开启微投新篇章！！！',
          roomList:[],
          selectRoom:'',
          countdown:'',
        }
    },
    mounted(){
        var vm = this
        vm.getRoomList()
    },
    watch:{
        runing(val){
            if(val){
                this.startGame()
            }else{
                this.stoptGame()
            }
        }
    },
    computed:{
        // 使用对象展开运算符将此对象混入到外部对象中
        ...mapState([
          'runing',
        ])
    },
    methods:{
        ...mapMutations([
            'merge'
        ]),
        startGame(){
            var vm =this
            vm.countdown = 70
            vm.info_game_start = '开始下注'
            let timer = setInterval(()=>{
                vm.countdown = vm.countdown-1
                if( vm.countdown <= 0){
                    clearInterval(timer)
                }
            },1000)
        },
        stoptGame(){
            var vm =this
            vm.info_game_start = '停止下注'
            vm.countdown = ''
        },
        initRoom(){
            var me = this
            me.merge({
                selectRoom:me.selectRoom
            })
        },
        initWebSocket(){
             console.log(store.state)
             var vm = this
            let userInfo = sessionStorage.getItem('userInfo')
            if(!store.state.userInfo.username && userInfo){
                userInfo = JSON.parse(userInfo)
                store.commit('merge',{
                    userInfo
                })
            }
            let obj = {
                username:store.state.userInfo.username,
                selectRoom:store.state.selectRoom
            }
            if(userInfo == null){
                setTimeout(()=>{
                    vm.initWebSocket()
                },100)
                return false
            }else{
                CHAT.init(obj)
                ADMIN_CHAT.init()
                // VIDEO_WS.init()
            }
        
            console.log(CHAT,'ww')
        },
        getRoomList(){
          const me = this
          const href = 'myapi/v1/room/roomlist/'
          me.$indicator.open();
          let FormData = ''
          me.axios({
              url:href,
              method:'POST',
              data:FormData
          }).then(({data})=>{
              if(data.code == 0){
                me.roomList = data.msg
               
                if(me.roomList && me.roomList.length>0){
                    me.selectRoom = me.roomList[0].room_id
                     me.merge({
                        roomList:me.roomList,
                        selectRoom:me.selectRoom
                    })
                }else{
                    me.$toast('房间为空')
                }
                me.initWebSocket()
              }else{
                me.$toast(data.detail)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
            //   if(err.response.data.detail == 'yours token is not allow to access.'){
            //        me.$indicator.close();
            //         window.sessionStorage.removeItem('token')
            //         me.$router.push('/')
            //   }
          })
        },
    }
}
</script>




