<template>
  <div>
      <div v-if="msgObj.msg_type == '8'">
          <system-msg  :msg="msgObj.content"></system-msg>
      </div>
      <div class="other-wrap" v-else>
        <span class="-header">
          <img src="../images/portrait.png" alt="">
        </span>
        <span class="-name">{{msgObj.from_username}}</span>
          <div :class="['other_msg',{'isAdmin':msgObj.msg_type == '5'}]" v-if="msgObj.msg_type == '5'">{{msgObj.content}}</div>
          <div :class="['other_msg',{'isAdmin_result':msgObj.msg_type == '6'}]" v-else-if="msgObj.msg_type == '6'" @click="show_bet(msgObj.content)">本局投注表</div>
          <div :class="['other_msg',{'isAdmin_result':msgObj.msg_type == '7'}]" v-else-if="msgObj.msg_type == '7'" @click="show_Closeaccount(msgObj.content)">本局玩家资料</div>
          <div :class="['other_msg','admin_msg','starting']" v-else-if="msgObj.msg_type == '3' && msgObj.msg_admin == '100'">
            <img src="../images/start70.gif" alt="" class="img">
          </div>
          <div :class="['other_msg','detial-result']" v-else-if="msgObj.msg_type == '4'" v-html="initZD(msgObj.content)" @click="show_Result()"></div>
          <div :class="['other_msg']" v-else>{{msgObj.content}}</div>
        <!-- msg_type: 6 showbacc需要弹窗显示的消息同时高亮
          msg_type: 7 jiesuan 需要弹窗显示的消息同时高亮 -->
          

            <showResult v-if="gotResult" v-on:showHide="show_Result()" :content="initResult(msgObj.content)" ></showResult>
            <show-this-bet v-if="showBet" v-on:showHide="showBet = false" :betlist="initDate(msgObj.content)" ></show-this-bet>
            <show-this-closeaccount v-if="showCloseaccount" v-on:showHide="showCloseaccount  = false" :betlist="initDate(msgObj.content)"></show-this-closeaccount>
      </div>
  </div>
</template>

<script>
import { mapState ,mapMutations } from 'vuex'
import SystemMsg from './SystemMsg'
import showResult from '../../components/showResult.vue'
import showThisBet from '../../components/showThisBet.vue'
import showThisCloseaccount from '../../components/showThisCloseaccount.vue'
export default {
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg:this.msgObj,
      gotResult:false,//开局 出结果 puker
      showBet:false,//展示 本局 投注表
      showCloseaccount:false,//展示 本局 投注表

    }
  },
  components:{showResult,showThisBet,showThisCloseaccount,SystemMsg},

  computed:{
    // 使用对象展开运算符将此对象混入到外部对象中
    ...mapState([
      'message_input',
      'message_bet',
      'bac_num',
      'userInfo',
    ])
  },
  mounted(){
    // console.log(this.msgObj)
    var vm =  this
    if(vm.msgObj.msg_type == 5 && vm.msgObj.to_nickname == vm.userInfo.nickname){
        vm.$toast(vm.msgObj.content)
    }
    if(vm.msgObj.msg_type == 4){
        vm.gotResult = true
    }
    this.$el.scrollIntoView()
  },
  props:{
    msgObj:{
      type:Object,
      default:{}
    }
  },
  methods:{
      ...mapMutations([
        'merge'
      ]),
      initResult(val){
          if(val){
            let result = JSON.parse(val)
            return result.result
          }
          return {}
      },
      initZD(val){
        val = this.initResult(val)
        if(!val || !val[2])return ''
        let result = val[2]
        let msg = ''
        switch(result){
          case 'z':
            msg =  '<h1>庄赢</h1>'
            break;
          case 'z,zd':
            msg =  '<h1>庄赢</h1><h1>庄对</h1>'
            break;
          case 'z,xd':
            msg =  '<h1>庄赢</h1><h1>闲对</h1>'
            break;
          case 'z,sd':
            msg =  '<h1>庄赢</h1><h1>庄闲对</h1>'
            break;
          case 'x':
            msg =  '<h1>闲赢</h1>'
            break;
          case 'x,xd':
            msg =  '<h1>闲赢</h1><h1>闲对</h1>'
            break;
          case 'x,zd':
            msg =  '<h1>闲赢</h1><h1>庄对</h1>'
            break;
          case 'x,sd':
            msg =  '<h1>闲赢</h1><h1>庄闲对</h1>'
            break;
          case 'h':
            msg =  '<h1>和局</h1>'
            break;
          case 'h,zd':
            msg =  '<h1>和局</h1><h1>庄对</h1>'
            break;
          case 'h,xd':
            msg =  '<h1>和局</h1><h1>闲对</h1>'
            break;
          case 'sb':
            msg =  '<h1>三宝</h1>'
            break;
        }
        return msg
      },
      initDate(val){
        // console.log(val.replace(/'/g, '"') )
        if(val instanceof String){
          val = val.replace(/'/g, '"') 
          return JSON.parse(val)
        }else{
          return val
        }
      
      },
      show_Result(){
          this.gotResult = !this.gotResult
      },
      show_bet(){
         this.showBet = !this.showBet
      },
      show_Closeaccount(){
          this.showCloseaccount = !this.showCloseaccount
      },
      hide_table(){
          this.merge({
              msg_admin:0
          })
      },
  }
}
</script>
<style lang='less'>
.other-wrap{
  /*background-color: #666;*/
  padding: 25px 5px 10px 60px;
  position: relative;
  .-header{
    display: block;
    position: absolute;
top: 15px;
    left: 10px;
    background-color: #905;
    height:50px;
    width: 50px;
    border-radius: 50%;
    font-weight: bolder;
     img{
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .-name{
    display: block;
    position: absolute;
    top: 0;
    left: 60px;
    font-size: 14px;
    padding: 2px;
    margin: 0 10px;
    color: #999;
  }
  .other_msg{
    display: inline-flex;
    background-color: #fff;
   padding: 12px;
    position: relative;
    left: 10px;
    top: 0px;
    border-radius: 6px;
    width: auto;
    max-width: ~'calc(100% - 100px)';
    font-size: 16px;
    line-height: 16px;
    .img{
      display: block;
      width: 120/16rem;
      height: 120/16rem;
    }
    &:before{
      content:'';
      display: inline-block;
      height: 0;
      width: 0;
      border-right: solid 10px #fff;
      border-top: solid 6px transparent;
      border-bottom : solid 6px transparent;
      position: absolute;
      top:5px;
      left: -8px;
    }
 
  }
  .admin_msg{

  }
  .detial-result{
        width: 10rem;
        // height: 5rem;
       top: 10px;
        background: #333;
        h1{
          display: block;
          width: 100%;
          text-align: center;
          color: red;
           font-size: 22px;
          line-height: 1.8rem;
          text-align: center;
          &:last-of-type{
            color: #ffffff;
          }
        }
        &:before{
          content:'';
          display: inline-block;
          height: 0;
          width: 0;
          border-right: solid 10px #333;
          border-top: solid 6px transparent;
          border-bottom : solid 6px transparent;
          position: absolute;
          top:5px;
          left: -8px;
        }
  }
  .isAdmin{
    background: orange;
      &:before{
      content:'';
      border-right: solid 10px orange;
    }
  }
  .isAdmin_result{
        background: linear-gradient(#e1cea3, #ca9d39);
        color: #515151;
        padding-left:1.8rem; 
 
        &:before{
        content:'';
        border-right: solid 10px #e1cea3;
      }
      &:after{
        content: '';
        display: block;
        width: 1.3rem;
        height: 1.3rem;
        display: block;
        position: absolute;
        top: 0.5rem;
        left: 0.5rem;
        background-image:url('../images/list.png');
        background-size:100% ;
      }
  }
}
</style>


