<template>
  <div id="Container">
      <headers></headers>
      <div id="myVideo">   
          <!-- <video src="rtmp://edc03.xpxyaba.com:443/edsource/2501" class="video"></video> -->
          <!-- <video-player  class="video-player-box video"
                 ref="videoPlayer"
                 :options="playerOptions"
                 :playsinline="true"
                 customEventName="customstatechangedeventname"
                    @ready="playerReadied">
    </video-player> -->
          <!-- <mt-spinner type="triple-bounce" class="loading" :size="30" color="rgb(100, 100, 100)"></mt-spinner>    -->
      </div>
      <div id="Box" :class="['Box_'+active,{'Box-active':ShowKeyboard}]">
            <ul class="selectTop clearfix">
                <li @click="switchTab(index)" :class="['item-select',{'item-active':active=='tab-'+index}]" v-for="(item,index) in tabList" :key="index">{{item}}</li>
            </ul>
            <mt-tab-container v-model="active" :swipeable='false'>
                <mt-tab-container-item id="tab-0">
                    <ludan></ludan>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-1">
                   <talk></talk>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-2">
                    <User-info></User-info>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-3">
                   <Game-recode :recordList="recordList"></Game-recode>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-4">
                    <Tip></Tip>
                </mt-tab-container-item>
            </mt-tab-container>
      </div>
    <!-- <showResult v-if="true" ></showResult> -->
      <keyboard :active="ShowKeyboard"></keyboard>
  </div>
</template>
<style lang="less" scoped>
#Container{
    display: block;
    #myVideo{
        margin-top: 2rem;
        position: relative;
        .video{
            display: block;
            width: 100%;
            height: 12.625rem;
        }
        .loading{
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -0.35rem;
            margin-left: -1rem;
        }
    }
    .Box-active{
        top: ~'calc(14.625rem - 263px)'!important;
    }
    #Box{
        display: block;
        position: fixed;
        top: 14.625rem;
        left: 0;
        right: 0;
        height: ~'calc(100% - 14.625rem)';
        .mint-tab-container,.mint-tab-container-wrap,.mint-tab-container-item{
            height: 100%;
        }
        .mint-tab-container-item{
                overflow-y: auto;
        }
        .selectTop{
            display: block;
            height: 2.5rem;
            background-color: #474a4f;
            .item-select{
                float: left;
                width: 20%;
                text-align: center;
                color: #fff;
                line-height: 2.5rem;
                height: 100%;
                font-size: 0.875rem;
            }
            .item-active{
                border-bottom: 2px solid #d89b4f;
               color: #d89b4f;
            }
        }
    }
    .Box_tab-2{
        background: #1f1f1e;
    }
}
</style>
<script>
// import showResult from '../components/showResult.vue'
import headers from '../components/header.vue'
import 'video.js/dist/video-js.css'
require('vue-video-player/src/custom-theme.css')
import { videoPlayer } from 'vue-video-player'
import 'videojs-flash'
import 'videojs-contrib-hls'

import {CHAT,ADMIN_CHAT} from '../assets/client'
import keyboard from '../components/keyboard.vue'
import ludan from './content/Ludan.vue'
import UserInfo from './content/UserInfo.vue'//用户信息
import Tip from './content/Tip.vue'//用户投注记录
import GameRecode from './content/GameRecode.vue'//用户投注记录
import talk from './content/talk.vue'
import pako from 'pako'
import {mapState,mapMutations} from 'vuex'
const u = navigator.userAgent
const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
export default {
    data(){
        return {
            username:'',
            password:'',
            active:'tab-0',
            tabList:[
                '路单','聊天下注','记录','历史记录','教学'
            ],
            recordList:[],//历史投注信息
            playerOptions: {
                live:true,
                autoplay:true,
                fluid:true,
                preload:'auto',
                techOrder:['flash','html5'],
                flash:{
                    hls:{
                        withCredentials:false
                    }
                },
                sources: {
                    withCredentials:false,
                    // type: 'rtmp/mp4',
                    type: 'application/x-mpegURL',
                    src: 'rtmp://edc06.xpxyaba.com:443/vipsource/table2501'
                },
                html5: { hls: { withCredentials: false }},//可以使用m3u8视频
                flash: { hls: { withCredentials: false }}, //可以使用rtmp视频
                // techOrder: ['flash'],
                height:540
            }
        }
    },
    components:{headers,ludan,UserInfo,talk,GameRecode,keyboard,videoPlayer,Tip},
    computed:{
        ...mapState([
            'ShowKeyboard',
            'msg_admin',
            'logout',
            'userInfo',
            'selectRoom'
        ]),
         player() {
            return this.$refs.videoPlayer.player
        }
    },
    watch:{
        logout(val){
            if(val){
                this.log_out()
            }
        },
        active(val){
            if(val == 'tab-2'){
                this.getUserInfo()
            }else  if(val == 'tab-3'){
                this.getRecord()
            }
        }
    },
    mounted(){
        var vm = this
    },
    methods:{
        ...mapMutations([
        'merge'
        ]),
         onPlayerPlay(player) {
        // console.log('player play!', player)
        },
        onPlayerPause(player) {
            // console.log('player pause!', player)
        },
        // ...player event
    
        // or listen state event
        playerStateChanged(playerCurrentState) {
            // console.log('player current update state', playerCurrentState)
        },
    
        // player is ready
        playerReadied(player) {
            console.log('the player is readied', player)
            // you can use it to do something...
            // player.[methods]
        },
        getRecord(){
          const me = this
          if(!me.selectRoom){
              setTimeout(()=>{
                  me.getRecord()
              },100)
              return false
          }
          const href = 'myapi/v1/room/getchatlist/'
          me.$indicator.open();
          let formData = 'room_name='+me.selectRoom
          me.axios({
              url:href,
              method:'POST',
              data:formData
          }).then(({data})=>{
              console.log(data)
              if(data.msg)me.recordList = data.msg
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        getUserInfo(){
            const me = this
           
            const href = 'myapi/v1/user/userinfo/'
            me.$indicator.open();
            let formData = 'username='+me.userInfo.username
            me.axios({
                url:href,
                method:'POST',
                data:formData
            }).then(({data})=>{
                console.log(data)
                if(data.code == 0){
                    this.merge({
                        userInfo:data.msg,
                        money:data.money
                    })
                }
                me.$indicator.close();
            }).catch((err)=>{
                console.log(err)
            })
        },
        log_out(){
            var me =this
            const href = 'myapi/v1/user/logout/'
            me.$indicator.open();
            me.axios({
                url:href,
                method:'POST',
            }).then(({data})=>{
                console.log(data)
                if(data.ret == 'ok'){
                    CHAT.logout()
                    ADMIN_CHAT.logout()
                    window.sessionStorage.removeItem('token')
                    window.sessionStorage.removeItem('userInfo')
                    me.$router.push('/login')
                }
                me.$indicator.close();
            }).catch((err)=>{
                console.log(err)
            })
        },
        switchTab(index){
            this.active = 'tab-'+index
        },
        createVideo(){
            var video = document.getElementById("myVideo");
            var ws = new WebSocket('wss://video.kkw100.com/p1');
            ws.onopen = function(){
             ws.send('hello')
            }
            ws.onmessage = function(e) {
                var blob = e.data;
                var reader = new FileReader();
                reader.readAsBinaryString(blob);
                reader.onload = function (evt) {
                    // var data = pako.inflate(evt.target.result, { to: 'string' })
                    // console.log(evt.target.result)
                };
            }
            /*
            分别注册 WebSocket 对象的 onopen、onclose、onerror 以及 onmessage 回调函数。
            通过ws.send()来进行发送数据，这里不仅可以发送字符串，也可以发送 Blob 或 ArrayBuffer 类型的数据。
            如果接收的是二进制数据，需要将连接对象的格式设为 blob 或 arraybuffer。
            */
           
        },
        submit(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        regist(){
            this.$router.push('/regist')
        }
    }
}
</script>




