import io from 'socket.io-client'
import store from '../store/index.js'
import { MessageBox } from 'mint-ui';
import pako from 'pako'
const CHAT={
	msgObj:document.getElementsByClassName("body-wrapper")[0],
	username:null,
	socket:null,
	onlineCount:0,
	onlineUsers:null,
	msgArr:[],
	//退出，本例只是一个简单的刷新
	logout:function(){
		this.socket.close()
	},
	//提交聊天消息内容
	submit:function(msg){
		if(msg){
			msg =JSON.stringify(msg)
			this.socket.send(msg);
		}else{
			console.log('msg is null')
		}
	},
	genUid:function(){
		return new Date().getTime()+""+Math.floor(Math.random()*899+100);
	},
	changeInfo(){
		this.userid = localStorage.getItem('userid');
		this.username = localStorage.getItem('name');
		this.color = localStorage.getItem('color');
		this.weichat = localStorage.getItem('weichat');
		this.socket.emit('changeInfo', {userid:this.userid, username:this.username,color:this.color,weichat:this.weichat});
	},
	init:function(obj){
		/*
		客户端根据时间和随机数生成uid,这样使得聊天室用户名称可以重复。
		实际项目中，如果是需要用户登录，那么直接采用用户的uid来做标识就可以
		*/
		if (!obj.username) {return}
		// this.username = Math.floor(Math.random()*10);
		
		//连接websocket后端服务器
		// this.socket = io.connect('wss://node.redream.cn');
		this.username = obj.username
		console.log(this.username)
		// this.socket = io.connect('ws://58.38.210.207:9999',{
		// 	path:'/ws/chat/room_1/'
		// });
		let token = decodeURI(sessionStorage.getItem('token'))
		let userInfo = decodeURI(sessionStorage.getItem('userInfo'))
		userInfo = JSON.parse(userInfo)
		this.socket = new WebSocket('ws://58.38.210.207:9999/ws/chat/'+obj.selectRoom+'/?username='+userInfo.username+'&token='+token);
		this.socket.onopen  = function(e){
			console.log('连接成功')
			console.log(e)
		}
		this.socket.onmessage  = function(e){
			console.log('截获短信成功')
			var data = JSON.parse(e.data)
			if(false){
				CHAT.logout()
			}
			//更改bac_num
			if(data.msg_type == 3  && data.msg_admin == 100){
				CHAT.msgArr = []
				store.commit('merge',{
					msg_admin:data.msg_admin,
					msg_type:data.msg_type,
			
					bac_num:data.bac_num,
					runing:true
				})
			}else if(data.msg_type == 3  && data.msg_admin != 100){
				store.commit('merge',{
					runing:false,
					// bac_num:'',
					msg_type:0,
				})
			}
			if(data.msg_type == 4){//本靴结果  结果在content里 
				let result_this = JSON.parse(data.content) 
				store.commit('merge',{
					roadMapHistory:result_this.result_list,
					tongji_ws:result_this.tongji
				})
			}
			console.log(data)
			CHAT.msgArr.push(data)	
		}
		this.socket.onclose  = function(e){
			console.log('主聊天断开')
		}
	}
}	
const ADMIN_CHAT={
	username:null,
	socket:null,
	onlineCount:0,
	onlineUsers:null,
	msgArr:[],
	//退出，本例只是一个简单的刷新
	logout:function(){
		this.socket.close()
	},
	//提交聊天消息内容
	submit:function(msg){
		if(msg){
			msg =JSON.stringify(msg)
			this.socket.send(msg);
		}else{
			console.log('msg is null')
		}
	},
	init:function(obj){
		//连接websocket后端服务器
		// this.socket = io.connect('wss://node.redream.cn');
		let token = decodeURI(sessionStorage.getItem('token'))
		let userInfo = decodeURI(sessionStorage.getItem('userInfo'))
		userInfo = JSON.parse(userInfo)
		this.socket = new WebSocket('ws://58.38.210.207:9999/ws/admin/admin/?username='+userInfo.username+'&token='+token);
		this.socket.onopen  = function(e){
			console.log('连接成功')
			console.log(e)
		}
		this.socket.onmessage  = function(e){
			console.log('截获ADMIN短信成功')
			var data = JSON.parse(e.data)
			ADMIN_CHAT.msgArr.push(data)
			if(data.msg_type == 0){//1. 0 为用户消息，这时候判断to_username 如果是给自己的， 就根据user_bev 来判断是那种用户行为（0 禁言， 1踢出用户， 2警告信息）前端弹出警告窗口， 警告内容就是为content内容
				if(data.to_use == userInfo.user_id){
					switch(data.user_bev){
						case 0:
							MessageBox('提示', data.content);
							store.commit('merge',{
								cancalMessage:true
							})
						break;
						case 1:
							MessageBox('提示', data.content);
							store.commit('merge',{
								logout:true
							})
						break;
						case 2:
							MessageBox('提示', data.content);
						break;
					}
				}
			}else if(data.msg_type == 1){// 1 为结果通知消息，判断room_name，是否是当前房间 content为当前房间下的路单信息。 根据这个信息来构建路单


			}	
			console.log(data)
		}
		this.socket.onclose  = function(e){
			console.log('adminCHat断开')
		}
	}
}	
