const CHAT_USER={
	socket:null,
	msgArr:[],
	init:function(obj){
		this.msgArr = []
		for(let i=0;i<obj.length;i++){
			let item = obj[i]
			CHAT_USER.msgArr.push(item)	
		}
		console.log(this.msgArr)
	}
}		
export  {CHAT,ADMIN_CHAT,VIDEO_WS,CHAT_USER}


