<template>
<div class="self-wrap">
  <span class="-header">
     <img :src="msg.avatal_url" alt="">
  </span>
  <!-- <span class="-name">超级厉害</span> -->
  <div class="-msg">{{msg.content}}</div>
</div>
</template>

<script>
export default {
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg:this.msgObj
    }
  },
  mounted(){
    console.log(this.msgObj)
  	this.$el.scrollIntoView()
  },
  props:["msgObj"]
}
</script>
<style lang='less'>
.self-wrap{
  /*background-color: #666;*/
 padding: 10px 5px 0px 5px;
  position: relative;
  overflow: hidden;
  .-header{
  	position: relative;
  	left: ~'calc(100% - 50px)';
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height:50px;
    width: 50px;
    border-radius: none;
    font-weight: bolder;
    img{
      display: block;
      width: 100%;
      height: 100%;
      background-image: url('../images/portrait.png');
      background-size: 100% 100%;
    }
  }
 /* .-name{
    background-color: #fff;
    position: absolute;
    font-size: 14px;
    padding: 2px;
    margin: 0 10px;
  }*/
  .-msg{
    background-color: #a0e75b;
    padding: 12px;

    float: right;
    position: relative;
    left: -62px;
    top: 0px;
    border-radius: 6px;
    max-width: ~'calc(100% - 100px)';
    font-size: 16px;
    line-height: 16px;
    &:before{
      content:'';
      display: inline-block;
      height: 0;
      width: 0;
      border-left: solid 10px #a0e75b;
      border-top: solid 6px transparent;
      border-bottom : solid 6px transparent;
      position: absolute;
      top:15px;
      left: 100%;
    }
  }
}
</style>


