<template>
  <div id="UserInfo">
      <div  class="clearfix">
        <div class="record_cell">
            <img class="portrait" @click="changeUserLogo"  :src="require('../images/user_'+userInfo.avatar_url+'.jpg')">
            <p class="integral">
                <span  class="" >{{userInfo.nickname}}</span>
                <span class="editNickname " @click="changeNickName">[修改]</span>
            </p>
            <p class="integral">
                <span class="">积分：</span>
                <span class="">{{money}}</span>
            </p>
        </div>
        <div class="record_cell" style="padding-top:1.5rem;">
            <button class="btn-info" @click="changePass" v-text="'修改密码'"></button>
        </div>
    </div>
    <button  class="refresh-history" @click="logOut">退出登录</button>
    <!-- <button  class="refresh-history">更新</button>
    <table cellpadding="0" cellspacing="0" class="record">
        <thead>
            <tr>
                <td  class="table-cell">台桌</td>
                <td  class="table-cell">靴 / 局</td>
                <td  class="table-cell">投注</td>
                <td  class="table-cell">总投注</td>
                <td  class="table-cell">总派彩</td>
                <td  class="table-cell">胜负</td>
                <td  class="table-cell">日期</td>
            </tr>
        </thead>
        <tbody class="tbody">
            <tr @click="getChatHistory(item)" ng-if="item.started" class="ng-scope">
                <td>
                    <span ng-bind="item.tableName" class="">23</span>
                </td>
                <td>
                    <span ng-bind="item.tableName" class="">23</span>
                </td>
                <td>
                    <span ng-bind="item.tableName" class="">23</span>
                </td>
                <td>
                    <span ng-bind="item.tableName" class="">23</span>
                </td>
                <td>
                    <span ng-bind="item.tableName" class="">23</span>
                </td>
                <td>
                    <span ng-bind="item.shoeNo" class="">4515</span>/<span class="">24</span>
                </td>
                <td>
                    <span class="time">{{1544104952587 | formatTime('YMDHMS')}}</span>
                </td>
            </tr>
        </tbody>
    </table> -->
    <transition name="fade">
        <change-password v-if="showCP" v-on:showHide="changePass"></change-password>
        <nike-name v-if="showCN" v-on:showHide="changeNickName"></nike-name>
        <user-logo v-if="showCL" v-on:showHide="changeUserLogo"></user-logo>
    </transition>
  </div>
</template>
<style lang="less" scoped>
#UserInfo{
    display: block;
    .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    }
    .record_cell{
        width: 50%;
        float: left;
        .portrait{
            display: block;
            width: 4.5rem;
            height: 4.5rem;
            margin: 1.5rem auto 1rem;
            border-radius: 5px;
            background-image:url('../images/portrait.png');
            background-size:100%; 
        }
        .integral{
            font-size: 1rem;
            color: #fff;
            text-align: center;
            margin-top: 0.25rem;
            .editNickname{
                color: red;
            }
        }
        .btn-info{
            color: #000;
            background-color: #aebc86;
            border-radius: 1vw;
            display: block;
            width: 40vw;
            line-height: 6vw;
            margin: 0 auto 0.75rem;
            font-size: 0.9rem;
            line-height: 0.9rem;
            padding: 5px 0;
            border: 0;
        }
    }
    button.refresh-history {
        border-radius: 12px;
        display: block;
        padding: 0;
        height: 2rem;
        width: 100%;
        background: #d89b4f;
        border: 0;
        font-size: 1.1rem;
        color: #ffffff;
        width: 80vw;
        margin: 20px auto;
        outline: none;
    }
    .record{
        width: 98vw;
        overflow: hidden;
        margin: 0 auto;
        font-size: 0.8rem;
        margin-top: 15px;
        margin-bottom: 50px;
        border-color: grey;
        thead {
            background-color: #767676;
            td {
                color: #fff;
                height: 2rem;
                text-align: center;
                border-right: 1px solid #333333;
            }
            td:first-child {
                border-top-left-radius: 0.3rem;
            }
            td:last-child {
                border-right: 0;
                border-top-right-radius: 0.3rem;
            }
        }
       
        tbody{
            background-color: #444444;
            min-height: 2rem;
             td {
                text-align: center;
                color: #fff;
                height: 2rem;
                border-right: 1px solid #333333;
                border-bottom: 1px solid #333333;
            }
        }
    }
}
</style>
<script>
import changePassword from '../../components/changePassword.vue'
import nikeName from '../../components/nikeName.vue'
import userLogo from '../../components/userLogo.vue'
import { mapState, mapMutations } from 'vuex'
export default {
    data(){
        return {
            username:'',
            password:'',
            noDisturbing:false,//开启免打扰
            showCP:false,//修改密码
            showCN:false,//修改昵称
            showCL:false,//修改头像
        }
    },
    mounted(){
        var vm = this
    },
    computed:{
        // 使用对象展开运算符将此对象混入到外部对象中
        ...mapState([
        'userInfo',
        'money'
        ])
    },
    components:{changePassword,nikeName,userLogo},
    methods:{
         ...mapMutations([
        'merge'
        ]),
        logOut(){
            alert('logout')
            this.merge({
                logout:true
            })
        },
        changePass(){
            this.showCP = !this.showCP
        },
        changeNickName(){
            this.showCN = !this.showCN
        },
         changeUserLogo(){
            this.showCL = !this.showCL
        },
     
    }
}
</script>




