<template>
  <div class="body-wrapper" ref="body_wrapper" @scroll="onscroll" @click="hideBoard">
    <template v-for="msgObj in CHAT.msgArr">
      <!-- <system-msg :msg="msgObj.user.username" v-if="msgObj.login"></system-msg> -->
      <template v-if="!msgObj.login&&!msgObj.logout">
        <other-msg v-if="msgObj.from_username != CHAT.username" :msgObj="msgObj" ></other-msg>
        <self-msg v-if="msgObj.from_username==CHAT.username" :msgObj="msgObj"></self-msg>
      </template>
    </template>
    <!-- <other-msg></other-msg> -->
    <a class="icon-todown" @click="pulldown()" v-if="showtodown"></a>
  </div>

</template>

<script>
import OtherMsg from './OtherMsg'
import SelfMsg from './SelfMsg'
import { mapMutations } from 'vuex'
import {CHAT} from '../../assets/client'
export default {
  data () {
    return {
      CHAT,
      showtodown:false,
    }
  },
  mounted(){

  },
  components:{
    OtherMsg,
    SelfMsg,
  },
  methods:{
    ...mapMutations([
      'merge'
    ]),
    hideBoard(){
        this.merge({
          ShowKeyboard:false
        })
    },
    onscroll(e){
      var scrollTop = e.target.scrollTop
      var scrollHeight = e.target.scrollHeight
      let swiperHeight =  e.target.clientHeight
      if (scrollHeight - scrollTop > swiperHeight + 50) {
        this.showtodown = true
      } else {
        this.showtodown = false
      }
    },
    pulldown(){
      var vm = this
      console.log(vm.$refs.body_wrapper.scrollHeight)
      let height =vm.$refs.body_wrapper.scrollHeight
      vm.$refs.body_wrapper.scrollTo(0,height)
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>
.mint-tab-container-wrap,.mint-tab-container-item{
    height: 100%;
}
.body-wrapper{
  background-color: #eee;
  height: ~'calc(100% - 105px)';
  overflow-y: scroll;
  color: #333;
  padding: 0 5px;
  transition: all ease 0.5;
  padding-bottom: 2rem;
  .icon-todown{
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
bottom: 9rem;
    right: 2rem;
    background-image:url('../images/down.png'); 
    background-size:100% 100%; 
  }
}

</style>


